/* Global styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
}

/* Custom styles for Portfolio component */
.portfolio {
  max-width: 80%;
  margin: auto;
  background-color: #f5f5f5;
  padding: 4rem 2rem;
  border-radius: 1rem;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.portfolio__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.portfolio__title {
  color: #333;
  font-size: 2rem;
  font-weight: bold;
}

.portfolio__subtitle {
  color: #666;
  font-size: 1.2rem;
  font-style: italic;
}

.portfolio__content {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.projects-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.project-card {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

.project-card img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.project-info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  padding: 1rem;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.project-info h2 {
  font-size: 1.5rem;
  font-weight: bold;
  margin-top: 0;
}

.project-info p {
  font-size: 1rem;
  line-height: 1.5;
}

.project-info a {
  display: inline-block;
  padding: 0.5rem 1rem;
  background-color: #333;
  color: white;
  border-radius: 0.25rem;
  text-decoration: none;
  transition: background-color 0.3s ease-in-out;
}

.project-info a:hover {
  background-color: #666;
}

.project-card:hover .project-info {
  opacity: 1;
  visibility: visible;
}